@import 'vars';
@import 'mixins';

.#{$prefix}resizer {
  position: absolute;
  right: 0;
  width: 8px;
  height: 100%;
  top: 0;
  bottom: 0;
  background: #ddd;
  z-index: 1;

  &__dots {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 8px;
    align-items: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: ew-resize; }

  &__dot {
    background: #fff;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    margin-bottom: 4px;

    &:last-child {
      margin-bottom: 0; } } }
